import React, {useState} from 'react';
import type {Meta, StoryObj} from '@storybook/react';
import {action} from '@storybook/addon-actions';
import {styled} from 'styled-components';
import {Autocomplete} from './autocomplete';

const Wrapper = styled.div`
  width: 100%;
  padding: 20px;
`;

const meta: Meta<typeof Autocomplete> = {
  title: 'Components/AutoComplete/BasicAutocomplete',
  component: Autocomplete,
  tags: ['autodocs'],
  parameters: {
    componentSubtitle: `import { Autocomplete } from 'react-restyle-components'`,
    layout: 'padded',
  },
  decorators: [
    (Story) => (
      <Wrapper>
        <Story />
      </Wrapper>
    ),
  ],
};
export default meta;
type Story = StoryObj<typeof Autocomplete>;

// Documentation/Overview story (appears first)
export const Documentation: Story = {
  name: 'Documentation',
  args: {
    value: '',
    options: ['Apple', 'Samsung', 'Xiaomi', 'Huawei', 'Oppo', 'Vivo'],
    filterConfig: {
      enabled: true,
      caseSensitive: false,
      minCharacters: 0,
    },
    uiConfig: {
      placeholder: 'Search mobile companies...',
      emptyStateMessage: 'No results found',
      showOnFocus: true,
      showOnClick: true,
    },
  },
  render: (args) => {
    const [value, setValue] = useState('');
    return (
      <Autocomplete
        {...args}
        value={value}
        onValueChange={(selectedValue) => {
          const newValue =
            typeof selectedValue === 'string'
              ? selectedValue
              : selectedValue.label;
          setValue(newValue);
          action('onValueChange')(selectedValue);
        }}
      />
    );
  },
  parameters: {
    docs: {
      source: {
        code: `
import { Autocomplete } from 'react-restyle-components';

<Autocomplete
  value={value}
  options={['Apple', 'Samsung', 'Xiaomi', 'Huawei', 'Oppo', 'Vivo']}
  filterConfig={{
    enabled: true,
    caseSensitive: false,
    minCharacters: 0,
  }}
  uiConfig={{
    placeholder: 'Search mobile companies...',
    emptyStateMessage: 'No results found',
    showOnFocus: true,
    showOnClick: true,
  }}
  onValueChange={(value) => console.log(value)}
/>
        `,
        language: 'tsx',
      },
      description: {
        story:
          'Overview of the Autocomplete component with default configuration. This component provides a searchable dropdown with filtering, customizable UI, and flexible value handling.',
      },
    },
    order: 0,
  },
};

